.one span {
	color: #444;
	opacity: 0;
	-webkit-transform: translate(-150px, -50px) rotate(-180deg) scale(3);
	        transform: translate(-150px, -50px) rotate(-180deg) scale(3);
	-webkit-animation: revolveScale 1.4s forwards;
	        animation: revolveScale 1.4s forwards;
}

@-webkit-keyframes revolveScale {
	60% {
		-webkit-transform: translate(20px, 20px) rotate(30deg) scale(.3);
		        transform: translate(20px, 20px) rotate(30deg) scale(.3);
	}

	100% {
		-webkit-transform: translate(0) rotate(0) scale(1);
		        transform: translate(0) rotate(0) scale(1);
		opacity: 1;
	}
}

@keyframes revolveScale {
	60% {
		-webkit-transform: translate(20px, 20px) rotate(30deg) scale(.3);
		        transform: translate(20px, 20px) rotate(30deg) scale(.3);
	}

	100% {
		-webkit-transform: translate(0) rotate(0) scale(1);
		        transform: translate(0) rotate(0) scale(1);
		opacity: 1;
	}
}

.two span {
	color: #444;
	opacity: 0;
	-webkit-transform: translate(200px, -100px) scale(2);
	        transform: translate(200px, -100px) scale(2);
	-webkit-animation: ballDrop .3s forwards;
	        animation: ballDrop .3s forwards;
}

@-webkit-keyframes ballDrop {
	60% {
		-webkit-transform: translate(0, 20px) rotate(-180deg) scale(.5);
		        transform: translate(0, 20px) rotate(-180deg) scale(.5);
	}

	100% {
		-webkit-transform: translate(0) rotate(0deg) scale(1);
		        transform: translate(0) rotate(0deg) scale(1);
		opacity: 1;
	}
}

@keyframes ballDrop {
	60% {
		-webkit-transform: translate(0, 20px) rotate(-180deg) scale(.5);
		        transform: translate(0, 20px) rotate(-180deg) scale(.5);
	}

	100% {
		-webkit-transform: translate(0) rotate(0deg) scale(1);
		        transform: translate(0) rotate(0deg) scale(1);
		opacity: 1;
	}
}

.three span {
	color: #444;
	opacity: 0;
	-webkit-transform: translate(-300px, 0) scale(0);
	        transform: translate(-300px, 0) scale(0);
	-webkit-animation: sideSlide 2.5s forwards;
	        animation: sideSlide 2.5s forwards;
}

@-webkit-keyframes sideSlide {
	60% {
		-webkit-transform: translate(20px, 0) scale(1);
		        transform: translate(20px, 0) scale(1);
		color: #444;
	}

	80% {
		-webkit-transform: translate(20px, 0) scale(1);
		        transform: translate(20px, 0) scale(1);
		color: #444;
	}

	99% {
		-webkit-transform: translate(0) scale(1.2);
		        transform: translate(0) scale(1.2);
		color: #444;
	}

	100% {
		-webkit-transform: translate(0) scale(1);
		        transform: translate(0) scale(1);
		opacity: 1;
		color: #444;
	}
}

@keyframes sideSlide {
	60% {
		-webkit-transform: translate(20px, 0) scale(1);
		        transform: translate(20px, 0) scale(1);
		color: #444;
	}

	80% {
		-webkit-transform: translate(20px, 0) scale(1);
		        transform: translate(20px, 0) scale(1);
		color: #444;
	}

	99% {
		-webkit-transform: translate(0) scale(1.2);
		        transform: translate(0) scale(1.2);
		color: #444;
	}

	100% {
		-webkit-transform: translate(0) scale(1);
		        transform: translate(0) scale(1);
		opacity: 1;
		color: #444;
	}
}

.four span {
	color: #444;
	opacity: 0;
	-webkit-transform: translate(0, -100px) rotate(360deg) scale(0);
	        transform: translate(0, -100px) rotate(360deg) scale(0);
	-webkit-animation: revolveDrop 1.3s forwards;
	        animation: revolveDrop 1.3s forwards;
}

@-webkit-keyframes revolveDrop {
	30% {
		-webkit-transform: translate(0, -50px) rotate(180deg) scale(1);
		        transform: translate(0, -50px) rotate(180deg) scale(1);
	}

	60% {
		-webkit-transform: translate(0, 20px) scale(.8) rotate(0deg);
		        transform: translate(0, 20px) scale(.8) rotate(0deg);
	}

	100% {
		-webkit-transform: translate(0) scale(1) rotate(0deg);
		        transform: translate(0) scale(1) rotate(0deg);
		opacity: 1;
	}
}

@keyframes revolveDrop {
	30% {
		-webkit-transform: translate(0, -50px) rotate(180deg) scale(1);
		        transform: translate(0, -50px) rotate(180deg) scale(1);
	}

	60% {
		-webkit-transform: translate(0, 20px) scale(.8) rotate(0deg);
		        transform: translate(0, 20px) scale(.8) rotate(0deg);
	}

	100% {
		-webkit-transform: translate(0) scale(1) rotate(0deg);
		        transform: translate(0) scale(1) rotate(0deg);
		opacity: 1;
	}
}

.five span {
	color: #444;
	opacity: 0;
	-webkit-transform: translate(0, -100px) rotate(360deg) scale(0);
	        transform: translate(0, -100px) rotate(360deg) scale(0);
	-webkit-animation: dropVanish .5s forwards;
	        animation: dropVanish .5s forwards;
}

@-webkit-keyframes dropVanish {
	30% {
		-webkit-transform: translate(0, -50px) rotate(180deg) scale(1);
		        transform: translate(0, -50px) rotate(180deg) scale(1);
	}

	50% {
		-webkit-transform: translate(0, 20px) scale(.8) rotate(0deg);
		        transform: translate(0, 20px) scale(.8) rotate(0deg);
		opacity: 1;
	}

	80% {
		-webkit-transform: translate(-100px, -100px) scale(1.5) rotate(-180deg);
		        transform: translate(-100px, -100px) scale(1.5) rotate(-180deg);
		opacity: 0;
	}

	100% {
		-webkit-transform: translate(0) scale(1) rotate(0deg);
		        transform: translate(0) scale(1) rotate(0deg);
		opacity: 1;
	}
}

@keyframes dropVanish {
	30% {
		-webkit-transform: translate(0, -50px) rotate(180deg) scale(1);
		        transform: translate(0, -50px) rotate(180deg) scale(1);
	}

	50% {
		-webkit-transform: translate(0, 20px) scale(.8) rotate(0deg);
		        transform: translate(0, 20px) scale(.8) rotate(0deg);
		opacity: 1;
	}

	80% {
		-webkit-transform: translate(-100px, -100px) scale(1.5) rotate(-180deg);
		        transform: translate(-100px, -100px) scale(1.5) rotate(-180deg);
		opacity: 0;
	}

	100% {
		-webkit-transform: translate(0) scale(1) rotate(0deg);
		        transform: translate(0) scale(1) rotate(0deg);
		opacity: 1;
	}
}

.six span {
	color: #444;
	opacity: 0;
	-webkit-transform: rotate(-180deg) translate(150px, 0);
	        transform: rotate(-180deg) translate(150px, 0);
	-webkit-animation: twister .5s forwards;
	        animation: twister .5s forwards;
}

@-webkit-keyframes twister {
	10% {
		opacity: 1;
	}
	100% {
		-webkit-transform: rotate(0deg) translate(0);
		        transform: rotate(0deg) translate(0);
		opacity: 1;
	}
}

@keyframes twister {
	10% {
		opacity: 1;
	}
	100% {
		-webkit-transform: rotate(0deg) translate(0);
		        transform: rotate(0deg) translate(0);
		opacity: 1;
	}
}

.seven span {
	color: #FFC330;
	font-size: 30px;
	opacity: 0;
	-webkit-transform: translate(-150px, 0) scale(.3);
	        transform: translate(-150px, 0) scale(.3);
	-webkit-animation: leftRight .5s forwards;
	        animation: leftRight .5s forwards;
}

@-webkit-keyframes leftRight {
	40% {
		-webkit-transform: translate(50px, 0) scale(.7);
		        transform: translate(50px, 0) scale(.7);
		opacity: 1;
		color: #348c04;
	}

	60% {
		color: #0f40ba;
	}

	80% {
		-webkit-transform: translate(0) scale(2);
		        transform: translate(0) scale(2);
		opacity: 0;
	}

	100% {
		-webkit-transform: translate(0) scale(1);
		        transform: translate(0) scale(1);
		opacity: 1;
	}
}

@keyframes leftRight {
	40% {
		-webkit-transform: translate(50px, 0) scale(.7);
		        transform: translate(50px, 0) scale(.7);
		opacity: 1;
		color: #348c04;
